
@import 'vars';
@import 'fonts';
@import 'reset';
@import 'libs';
@import 'global';

body{
   background-color: var(--background);

}
.color-theme{
   cursor: pointer;
   display: flex;
   align-items: center;
   &__btn{
      margin-right: 20px;
      background-color: transparent;
      display: flex;
      justify-content: start;
      align-items: center;
      padding-left: 3px;
      padding-right: 3px;
      width: 52px;
      height: 32px;
      border-radius: 100px;
      border: 3px solid var(--icons-color);
   }
   &__text{
      color: var(--border-color);
      margin-right: 10px;
   }
   &__btn_right{
      justify-content: end;
   }
   &__item{
      height: 20px;
      width: 20px;
      background-color: var(--icons-color);
      border-radius: 50%;
      transition: all 1s;
   }
}
.header{
   &__settings{
      display: none;
   }
   height: 7vh;
   width: 100%;
   border-bottom: solid var(--border-color) 2px;
   background-color: var(--background-content);
   margin-bottom: 20px;
   &__container{
      padding: 0px $paddingDesktop;
      height: 100%;
      margin: 0 auto;
      display: flex;
      justify-content: space-between;
      align-items: center;   
      max-width: 1200px;
   }
   &__content{
      display: flex;
      justify-content: space-between;
      align-items: center;
   }
}
.logo{
   display: flex;
   align-items: center;
   cursor: pointer;
   svg{
      margin-right: 10px;
   }
}
.profile_header{
   cursor: pointer;
   display: flex;
   align-items: center;
   &__img{
      margin-right: 10px;
      height: $profileHeight;
      width: $profileHeight;
      border-radius: 50%;
      border: 1px solid var(--border-color);
   }
}
.changeLanguage{
   cursor: pointer;
   user-select: none;
   margin-right: 40px;
   display: flex;
   justify-content: center;
   align-items: center;
   height: 32px;
   width: 32px;
   border-radius: 5px;
   border: 2px solid var(--icons-color);
   background-color: var(--border-color);
   color: var(--text-color);
   font-weight: 700;
   text-align: center;
   line-height: 29px;
   font-size: 14px;
}
.main{
   width: 100%;
   height: 100%;
   overflow-y: scroll;

}
.news{
   &__article{
      @extend %block;
   }
}
.communities{
   background-color: var(--background-content);
   width: 100%;
   height: 100%;
   padding: $paddingDesktop;
   border: solid var(--border-color) 2px;
   border-radius: $borderRadiusDesktop;
}
.messenger{
   background-color: var(--background-content);
   width: 100%;
   height: 100%;
   padding: $paddingDesktop;
   border: solid var(--border-color) 2px;
   border-radius: $borderRadiusDesktop;
}
.friends{
   background-color: var(--background-content);
   width: 100%;
   height: 100%;
   padding: 15px;
   border: solid var(--border-color) 2px;
   border-radius: 16px;
}
.recommendations{
   &__article{
      @extend %block;

   }
}
.settings{
   background-color: var(--background-content);
   width: 100%;
   height: 100%;
   padding: $paddingDesktop;
   border: solid var(--border-color) 2px;
   border-radius: $borderRadiusDesktop;
}
.profile{
   height: 100%;
   &__info{
      @extend %block;
      padding: 0px;
      background-color: var(--icons-color);
      height: auto;
      border-radius: $borderRadiusDesktop
   }

   &__info-content{
      margin-top: 10vh;
      background-color: var(--background-content);
      padding: 0px 15px 0px 0vh;
      display: flex;
      align-items: start;
      position: relative;
      justify-content: space-between;
      min-height: 10vh;
      border-radius: 14px;
      //border: 2px solid var(--border-color);
   }
   &__ava{
      height: 15vh;
      width: 15vh;
      border-radius: 50%;
      border: 1px solid var(--border-color);
      position: absolute;
      top: -7.5vh;
      left: 15px;
   }
   &__info-text{
      padding: 15px 0px 0px 20vh;
      // margin-top: -50px;
   }
   &__name{
      font-size: 28px;
      // text-shadow:  5px 5px 3px  var(--background),
      // -5px -5px 3px  var(--background);
   }
   &__seeMore{
      color: var(--icons-color)
   }
   &__settings{
      margin-top: 5vh; 
      transform: translateY(-50%)
   }
   &__friends{
      @extend %block;
   }
   &__photoes{
      @extend %block;
   }
   &__posts{
      @extend %block;
   }
   // &__background{
   //    background-color: var(--icons-color);
   //    border-radius: 16px 16px 0px 0px;
   //    width: 100%;
   //    height: 12vh;  
   //    display: flex;
   //    align-items: center;
   //    justify-content: center;
   // }


}
.aside{
   margin-right: 20px;
   min-width: 220px;
   border: solid var(--border-color) 2px;
   background-color: var(--background-content);
   border-radius: 16px;
   padding: 15px 5px 15px 5px;
   ul{
      height: 100%;
      position: relative;
      display: flex;
      flex-direction: column;
   }
   li{
      user-select: none;
      display: flex;
      align-items: center;
      height: 30px;
      width: 100%;
      padding-left: 5px;
      padding-right: 5px;
      border-radius: 5px;
      &:hover{
         background-color: var(--border-color);
      }
      cursor: pointer;
      line-height: 20px;
   }
   &__messenger{
      svg{
         margin-right: 7px;
      }
   }
   &__news{
      svg{
         margin-right: 3px;
      }
   }
   &__recommendations{
      svg{
         margin-right: 3px;
      }
   }
   &__friends{
      svg{
         margin-right: 7px;
      }
   }
   &__communities{
      svg{
         margin-right: 3px;
      }
   }
   &__settings{
      position: absolute;
      bottom: 0;
      left: 0;
      svg{
         margin-right: 3px;
      }
   }
}
.login{
   width: 100%;
   margin: 0 auton;
   background-color: var(--background);
   border: solid var(--border-color) 2px;
   padding: 30px 10px;
   border-radius: 16px;
   &__title{
      cursor: pointer;
      user-select: none;
      text-align: center;
      span{
         font-size: 16px;
         &.active{
            font-size: 48px;
         }
         &.login__title_slash{
            font-size: 26px;
         }
      }
      &_slash{
         font-size: 32px;
      }
   }
   &__btn{
      display: block;
      margin: 0 auto;
      padding: 5px 15px;
      font-size: 14px;
      background-color: transparent;
      color: var(--text-color);
      border: 2px solid var(--icons-color);
      border-radius: 5px;
      cursor: pointer;   
   }
}
.mobile-nav{
   display: none;
}
@import 'media';
